<!-- components/ButtonComponent.vue -->

<template>
  <button
    :type="buttonType"
    class="relative flex items-center justify-center"
    :class="[
      !showSlot ? buttonClasses : '',
      isDisabled || isLoading ? 'cursor-auto' : 'cursor-pointer',
    ]"
    :disabled="isDisabled || isLoading"
  >
    <template v-if="showSlot">
      <slot></slot>
    </template>
    <template v-else>
      <div
        :class="getTextColor"
        class="relative flex items-center justify-center"
      >
        <div
          class="absolute inset-0 flex items-center justify-center"
          :class="[isLoading ? 'opacity-100' : 'opacity-0']"
        >
          <div :class="loaderClasses">
            <svg
              xmlns="http://www.w3.org/2000/svg"
              class="h-full w-full animate-spin"
              viewBox="0 0 17 16"
              fill="none"
            >
              <path
                fill-rule="evenodd"
                clip-rule="evenodd"
                d="M10.148 2.92737C9.07688 2.57936 7.92305 2.57937 6.8519 2.92742C5.78075 3.27547 4.84729 3.95368 4.18528 4.86485C3.52327 5.77603 3.16672 6.87339 3.16671 7.99967C3.1667 9.12594 3.52324 10.2233 4.18523 11.1345C4.84723 12.0457 5.78069 12.7239 6.85183 13.072C7.92297 13.42 9.07681 13.4201 10.148 13.0721C11.2191 12.7241 12.1526 12.0459 12.8147 11.1347C13.4767 10.2236 13.8333 9.12624 13.8334 7.99997C13.8334 7.63178 14.1319 7.33331 14.5001 7.33333C14.8683 7.33335 15.1667 7.63184 15.1667 8.00003C15.1666 9.40787 14.7209 10.7796 13.8933 11.9185C13.0658 13.0574 11.8989 13.9051 10.5599 14.3401C9.221 14.7752 7.7787 14.7751 6.43978 14.34C5.10085 13.905 3.93402 13.0572 3.10653 11.9182C2.27904 10.7792 1.83336 9.4075 1.83337 7.99966C1.83338 6.59181 2.27908 5.22011 3.10659 4.08114C3.9341 2.94217 5.10093 2.09441 6.43987 1.65935C7.7788 1.22429 9.2211 1.22427 10.56 1.65929C10.9102 1.77306 11.1019 2.14916 10.9881 2.49933C10.8743 2.8495 10.4982 3.04114 10.148 2.92737Z"
                fill="currentColor"
              />
            </svg>
          </div>
        </div>
        <div
          class="relative flex items-center justify-center"
          :class="!isLoading ? 'opacity-100' : 'opacity-0'"
        >
          <div
            v-if="
              (image || icon || iconSlot) &&
              (iconPosition === 'leading' || iconPosition === 'only')
            "
            :class="iconClasses"
          >
            <img v-if="image" :src="image" :class="iconClasses" alt="Icon" />
            <!-- eslint-disable vue/no-v-html -->
            <div v-else-if="svgContent" v-html="svgContent" />
            <!-- eslint-enable -->
            <slot v-else-if="iconSlot" name="leading-icon"></slot>
          </div>
          <slot
            v-if="labelSlot && iconPosition !== 'only'"
            name="label-content"
          ></slot>
          <span v-else-if="iconPosition !== 'only'" :class="getTextColor">
            {{ label }}
          </span>
          <div
            v-if="(image || icon || iconSlot) && iconPosition === 'trailing'"
            :class="iconClasses"
          >
            <img v-if="image" :src="image" :class="iconClasses" alt="Icon" />
            <!-- eslint-disable vue/no-v-html -->
            <div v-else-if="svgContent" v-html="svgContent" />
            <!-- eslint-enable -->
            <slot v-else-if="iconSlot" name="trailing-icon"></slot>
          </div>
        </div>
      </div>
    </template>
  </button>
</template>

<script>
export default {
  props: {
    buttonType: {
      type: String,
      default: "",
    },
    type: {
      type: String,
      default: "brand",
      validator: (value) =>
        [
          "brand",
          "destructive",
          "neutral",
          "metal",
          "success",
          "brand-secondary",
        ].includes(value),
    },
    size: {
      type: String,
      default: "large",
      validator: (value) => ["small", "large"].includes(value),
    },
    emphasis: {
      type: String,
      default: "primary",
      validator: (value) =>
        ["primary", "secondary", "tertiary"].includes(value),
    },
    iconPosition: {
      type: String,
      default: "none",
      validator: (value) =>
        ["leading", "trailing", "only", "none"].includes(value),
    },
    ringOffsetColor: {
      type: String,
      default: "",
    },
    isDisabled: {
      type: Boolean,
      default: false,
    },
    isLoading: {
      type: Boolean,
      default: false,
    },
    icon: {
      type: String,
      default: "",
    },
    image: {
      type: String,
      default: "",
    },
    width: {
      type: String,
      default: "",
    },
    label: {
      type: String,
      default: "",
    },
    showSlot: {
      type: Boolean,
      default: false,
    },
    iconSlot: {
      type: Boolean,
      default: false,
    },
    labelSlot: {
      type: Boolean,
      default: false,
    },
  },
  data() {
    return {
      svgContent: "",
    };
  },
  computed: {
    buttonClasses() {
      const baseTransitions = "transition-transform ease-in-out duration-200";
      return [
        baseTransitions,
        this.getActiveState,
        this.getFocusState,
        this.getButtonWidth,
        this.getBackgroundColor,
        this.getPaddingAndBorderRadiusClasses,
        // this.getTextColor,
        this.getTextStyle,
      ];
    },
    getPaddingAndBorderRadiusClasses() {
      let output = "p-spacing-150 ipad:p-spacing-200 rounded-08";
      if (this.emphasis === "tertiary") {
        output = "p-spacing-0 rounded-04";
      } else if ((this.icon || this.iconSlot) && this.iconPosition === "only") {
        if (this.size === "small") {
          output = "p-spacing-100 rounded-04";
        } else {
          output = "p-spacing-150 rounded-[7px]";
        }
      } else if (this.size === "small") {
        output = "py-spacing-100 px-spacing-200 rounded-04";
      }
      return output;
    },
    getActiveState() {
      let output = "active:scale-95";
      if (this.isLoading || this.isDisabled) {
        output = "";
        // output = 'active:focus:ring-0'
      }
      return output;
    },
    getFocusState() {
      // ring corner radius = button radius + ring-offset
      let output =
        "focus:outline-none focus-visible:ring-2 focus-visible:ring-offset-2 focus-visible:ring-ring-primary";
      if (this.isLoading || this.isDisabled) {
        output = "";
      }
      if (output) {
        if (this.ringOffsetColor) {
          output = output + " " + `${this.ringOffsetColor}`;
        } else {
          output =
            output + " " + "focus-visible:ring-offset-bg-neutral-default";
        }
      }
      return output;
    },
    getBackgroundColor() {
      let output = "bg-transparent";
      if (this.isDisabled && this.emphasis !== "tertiary") {
        output = "bg-bg-neutral-disabled";
      } else {
        /* eslint-disable  no-lonely-if */
        if (this.type === "brand") {
          if (this.emphasis === "primary") {
            output =
              "bg-constant-brand-primary hover:bg-hover-constant-brand-primary";
          } else if (this.emphasis === "secondary") {
            output = "bg-bg-brand-primary hover:bg-hover-bg-brand-primary";
          } else if (this.emphasis === "tertiary") {
            output = "bg-transparent";
          }
        } else if (this.type === "brand-secondary") {
          if (this.emphasis === "primary") {
            output =
              "bg-constant-brand-secondary hover:bg-hover-constant-brand-secondary";
          } else if (this.emphasis === "secondary") {
            output = "bg-bg-brand-secondary hover:bg-hover-bg-brand-secondary";
          } else if (this.emphasis === "tertiary") {
            output = "bg-transparent";
          }
        } else if (this.type === "success") {
          if (this.emphasis === "primary") {
            output =
              "bg-constant-status-success hover:bg-hover-constant-status-success";
          } else if (this.emphasis === "secondary") {
            output = "bg-bg-status-success hover:bg-hover-bg-status-success";
          } else if (this.emphasis === "tertiary") {
            output = "bg-transparent";
          }
        } else if (this.type === "neutral") {
          if (this.emphasis === "primary") {
            output = "bg-bg-neutral-raised hover:bg-hover-bg-neutral-raised";
          } else if (this.emphasis === "secondary") {
            output = "bg-bg-neutral-raised hover:bg-hover-bg-neutral-raised";
          } else if (this.emphasis === "tertiary") {
            output = "bg-transparent";
          }
        } else if (this.type === "destructive") {
          if (this.emphasis === "primary") {
            output =
              "bg-constant-status-error hover:bg-hover-constant-status-error";
          } else if (this.emphasis === "secondary") {
            output = "bg-bg-status-error hover:bg-hover-bg-status-error";
          } else if (this.emphasis === "tertiary") {
            output = "bg-transparent";
          }
        } else if (this.type === "metal") {
          if (this.emphasis === "primary") {
            output = "dark bg-bg-neutral-raised hover:bg-bg-neutral-sunken";
          } else if (this.emphasis === "secondary") {
            output = "dark bg-bg-neutral-raised hover:bg-bg-neutral-sunken";
          } else if (this.emphasis === "tertiary") {
            output = "dark bg-transparent";
          }
        }
        /* eslint-enable */
      }
      return output;
    },
    getTextColor() {
      let output = "text-content-on-neutral-primary";
      if (this.isDisabled && this.emphasis !== "tertiary") {
        output = "text-content-on-neutral-tertiary";
      } else {
        /* eslint-disable  no-lonely-if */
        if (this.type === "brand") {
          if (this.emphasis === "primary") {
            output = "text-content-on-neutral-on-inverse";
          } else if (this.emphasis === "secondary") {
            output = "text-constant-brand-primary";
          } else if (this.emphasis === "tertiary") {
            output =
              "text-constant-brand-primary hover:text-hover-constant-brand-primary";
          }
        } else if (this.type === "brand-secondary") {
          if (this.emphasis === "primary") {
            output = "text-content-on-neutral-on-inverse";
          } else if (this.emphasis === "secondary") {
            output = "text-constant-brand-secondary";
          } else if (this.emphasis === "tertiary") {
            output =
              "text-constant-brand-secondary hover:text-hover-constant-brand-secondary";
          }
        } else if (this.type === "success") {
          if (this.emphasis === "primary") {
            output = "text-content-on-neutral-on-inverse";
          } else if (this.emphasis === "secondary") {
            output = "text-constant-status-success";
          } else if (this.emphasis === "tertiary") {
            output =
              "text-constant-status-success hover:text-hover-constant-status-success";
          }
        } else if (this.type === "neutral") {
          if (this.emphasis === "primary") {
            output = "text-content-on-neutral-primary";
          } else if (this.emphasis === "secondary") {
            output = "text-content-on-neutral-primary";
          } else if (this.emphasis === "tertiary") {
            output = "text-content-on-neutral-primary";
          }
        } else if (this.type === "destructive") {
          if (this.emphasis === "primary") {
            output = "text-content-on-neutral-primary";
          } else if (this.emphasis === "secondary") {
            output = "text-constant-status-error";
          } else if (this.emphasis === "tertiary") {
            output =
              "text-constant-status-error hover:text-hover-constant-status-error";
          }
        } else if (this.type === "metal") {
          if (this.emphasis === "primary") {
            output =
              "drop-shadow-metal-text bg-gradient-to-b from-[#FFF] from-[29.17%] to-[#C0C0C0] to-[75%] text-transparent bg-clip-text";
          } else if (this.emphasis === "secondary") {
            output =
              "drop-shadow-metal-text bg-gradient-to-b from-[#FFF] from-[29.17%] to-[#C0C0C0] to-[75%] text-transparent bg-clip-text";
          } else if (this.emphasis === "tertiary") {
            output =
              "drop-shadow-metal-text bg-gradient-to-b from-[#FFF] from-[29.17%] to-[#C0C0C0] to-[75%] text-transparent bg-clip-text";
          }
        }
        /* eslint-enable */
      }
      return output;
    },
    getTextStyle() {
      let output = "text-body/l/relaxed/500";
      if (this.size === "small") {
        output = "text-body/m/relaxed/500";
      }
      return output;
    },
    getButtonWidth() {
      let output = "";
      if (this.width) {
        output = this.width;
      } else if ((this.icon || this.iconSlot) && this.iconPosition === "only") {
        output = "w-fit";
      } else if (this.size === "small") {
        output = "w-fit";
      } else if (this.emphasis === "tertiary") {
        output = "w-fit";
      } else if (this.size === "large") {
        output = "w-full";
      }
      return output;
    },
    iconClasses() {
      let baseClasses = ["h-4", "w-4"];

      if (
        this.size === "small" &&
        (this.icon || this.iconSlot) &&
        this.iconPosition !== "only"
      ) {
        baseClasses = ["h-3", "w-3"];
      } else if (
        this.size === "large" &&
        (this.icon || this.iconSlot) &&
        this.iconPosition === "only"
      ) {
        baseClasses = ["h-6", "w-6"];
      }

      const positionClasses = {
        leading:
          this.size === "large" && this.emphasis !== "tertiary"
            ? "mr-2"
            : "mr-2",
        trailing:
          this.size === "large" && this.emphasis !== "tertiary"
            ? "ml-2"
            : "ml-2",
        only: "",
        none: "hidden",
      };

      return [
        ...baseClasses,
        positionClasses[this.iconPosition],
        this.getTextColor,
      ];
    },
    loaderClasses() {
      let baseClasses = ["h-4", "w-4"];

      if (
        this.size === "small" &&
        (this.icon || this.iconSlot) &&
        this.iconPosition !== "only"
      ) {
        baseClasses = ["h-3", "w-3"];
      } else if (
        this.size === "large" &&
        (this.icon || this.iconSlot) &&
        this.iconPosition === "only"
      ) {
        baseClasses = ["h-6", "w-6"];
      }
      return [...baseClasses, this.getTextColor];
    },
  },
  async mounted() {
    await this.parseSvg();
  },
  methods: {
    async parseSvg() {
      if (this.icon) {
        try {
          const resp = await fetch(this.icon);
          if (resp.ok) {
            const svgText = await resp.text();
            this.svgContent = svgText;
          }
        } catch (error) {
          console.error("error", error);
        }
      }
    },
  },
};
</script>

<style scoped>
/* You can style your button based on the provided design here */
/* Use the provided classes such as type, size, and emphasis for styling. */
</style>
